
import React, { Component } from "react";
import "./Css/Signup.css";
import { Link } from "react-router-dom";
import Card from '@mui/material/Card';
import instaLogo from '../images/insta_logo.jpg';
import { Button, CardActions, CardContent, TextField } from "@mui/material";
import Box from '@mui/material/Box';



export default class Signup extends Component {
    constructor(props){
        super(props);
        this.state={
          username:'',
          email:'',
          password:''
        }
      }

      handleClick = () => {
   
       this.props.signup(this.state.email, this.state.password);
      }

      handleGoogleClick = () => {
    this.props.signupwithGoogle();
      }
  render() {
    return (
      <div className="loginWrapper">


        <div className="loginCard">
        <Card variant="outlined">
          <div className="insta_logo">
            <img src={instaLogo} alt="" />
          </div>

          <CardContent>
            <Box m={2}>
            <TextField label="Email"  variant="outlined" fullWidth={true} onChange={(e)=> this.setState({ email: e.target.value})}/>
            </Box>
            <Box m={2}>
            <TextField label="Username"  variant="outlined" fullWidth={true} onChange={(e)=> this.setState({ username: e.target.value})}/>
            </Box>
            <Box m={2}>
            <TextField label="Password" className="password_input" type="password" variant="outlined" fullWidth={true} onChange={(e)=> this.setState({password: e.target.value})}/> 
            </Box>


          </CardContent>

          <CardActions className="buttons">
            <Button color="primary"  fullWidth={true} variant="contained" onClick={this.handleClick}> Sign up </Button> 
            <Button color="primary" fullWidth={true} variant="contained" onClick={this.handleGoogleClick}>Google Signup</Button>
          </CardActions>
        </Card>

        <Card variant="outlined" className="card2">
        <p className="no_account">Already have an account? <span className="sign_up"> <Link to="/Login">log in</Link> </span></p>
      
        </Card>
        </div>
      </div>
    );
  }
}
