import './App.css';
import Login from './Components/Login.js';
import Signup from './Components/Signup.js';
import React, { Component } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './firebase.js';
import { createUserWithEmailAndPassword, signInWithEmailAndPassword, getAuth, signInWithPopup, GoogleAuthProvider } from 'firebase/auth';


const auth = getAuth();
const provider = new GoogleAuthProvider();

export default class App extends Component {

  signup = (email,password) => {
    return createUserWithEmailAndPassword(auth,email,password)
  }

  login = (email, password) => {
    return signInWithEmailAndPassword(auth,email,password);
  }

  signupwithGoogle =() => {
    signInWithPopup(auth, provider)
    .then((result) => {
      // This gives you a Google Access Token. You can use it to access the Google API.
      const credential = GoogleAuthProvider.credentialFromResult(result);
      const token = credential.accessToken;
      console.log(token, "hello world")
      // The signed-in user info.
      const user = result.user;
      // IdP data available using getAdditionalUserInfo(result)
      // ...
    }).catch((error) => {
      // Handle Errors here.
      const errorCode = error.code;
      const errorMessage = error.message;
      // The email of the user's account used.
      const email = error.customData.email;
      // The AuthCredential type that was used.
      const credential = GoogleAuthProvider.credentialFromError(error);
      // ...
    });
  }
  render() {
    return (
      <div>
        <BrowserRouter>
        <Routes>
          <Route path="/Login" element={<Login login={this.login} signupwithGoogle={this.signupwithGoogle}/>}></Route>
          <Route path="/Signup" element={<Signup signup={this.signup} signupwithGoogle={this.signupwithGoogle}/>}></Route>
        </Routes>
        </BrowserRouter>
      </div>
    )
  }
}

